import styles from './Login.module.css'
import { FC } from 'react'
import { useForm } from 'react-hook-form'

const Login: FC = () => {
  const {
    register,
    handleSubmit,
    formState: { errors, isValid },
    reset,
  } = useForm({ mode: 'onBlur' })

  const onSubmit = (data: any) => {
    console.log(data)
    reset()
  }

  return (
    <form className={styles.login} onSubmit={handleSubmit(onSubmit)}>
      <div className={styles.formGroup}>
        <label htmlFor="h">
          Email:
          <input
            type="text"
            id="h"
            {...register('email', {
              required: ' Email   !!',
              minLength: { value: 6, message: '  6' },
              maxLength: { value: 30, message: '  30' },
              pattern: {
                value: /^[A-Za-z]\S+@\S+\.\S+$/g,
                message: '    !',
              },
            })}
          />
        </label>
      </div>
      <div className={styles.formGroup}>
        <label htmlFor="k">
          Password:
          <input
            type="password"
            id="k"
            {...register('password', {
              required: ' Password   !!',
              minLength: { value: 8, message: '  8' },
              maxLength: { value: 20, message: '  20' },
            })}
          />
        </label>
      </div>
      <div className={styles.formGroup2}>
        <label htmlFor="l">
          Remember me:
          <input
            type="checkbox"
            id="l"
            {...register('check', { required: false })}
          />
        </label>
      </div>
      {errors?.email && (
        <p className={styles.error}>
          {/* @ts-ignore */}
          {errors?.email?.message || ' Email   !'}
        </p>
      )}
      {errors?.password && (
        <p className={styles.error}>
          {/* @ts-ignore */}
          {errors?.password?.message ||
            ' Password   !'}
        </p>
      )}
      <div className={styles.formBtn}>
        <button disabled={true}>Send</button>
      </div>
    </form>
  )
}

export default Login

